___CSS_LOADER_EXPORT___.push([module.id, `/* ===== SEARCH BOX ===== */
.search {
    display: flex;
    justify-content: end;
}

.search_content {
    width: 100%;
    border-radius: 0.7em;
}

.search_input_container {
    justify-content: end;
    align-content: center;
    display: flex;
}

.open_search_input_btn {
    display: flex;
    justify-content: end;
    padding: 12px 26px 12px 26px;
}

.clear_search_input_btn {
    cursor: pointer;
    justify-content: center;
    align-items: center;
    position: absolute;
    margin-top: 1ex; /* equal to search_input top padding+ search top padding */
}

.search_input {
    width: 100%;
    border-radius: 0.7em;
    padding: 1ex;
}

.search_input:focus {
    outline: none;
}

.search_row {
    list-style-type: none;
    font-family: monospace;
    cursor: pointer;
}

.search_result_list {
    position: absolute;
    z-index: 30;
    list-style: none;
    right: 0;
    left: 0;
    margin-top: 0;
    margin-left: 0;
    padding-left: 1.2em;
    padding-right: 0.2em;
    margin-bottom: 1px;
    border-radius: 0.7em;
    overflow-x: hidden;
    overflow-y: auto;
    max-height: 5em;
}

.filter {
    cursor: pointer;
    font-family: monospace;
    border-radius: 500px;
    left: 0;
    padding-left: 10%;
}

.active_filters_list {
    list-style: none;
    margin: 0;
    padding: 0;
}

.close {
    color: black;
    float: right;
    padding-right: 5px;
}

.close:hover {
    opacity: 1;
}

form {
    padding-top: 0;
}

.filter_highlight_content {
    display: flex;
    justify-content: space-between;
    width: 100%;
}
.atom_string {
    flex-grow: 1;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
}`, "",{"version":3,"sources":["webpack://./src/lib/fragments/search.css"],"names":[],"mappings":"AAAA,2BAA2B;AAC3B;IACI,aAAa;IACb,oBAAoB;AACxB;;AAEA;IACI,WAAW;IACX,oBAAoB;AACxB;;AAEA;IACI,oBAAoB;IACpB,qBAAqB;IACrB,aAAa;AACjB;;AAEA;IACI,aAAa;IACb,oBAAoB;IACpB,4BAA4B;AAChC;;AAEA;IACI,eAAe;IACf,uBAAuB;IACvB,mBAAmB;IACnB,kBAAkB;IAClB,eAAe,EAAE,0DAA0D;AAC/E;;AAEA;IACI,WAAW;IACX,oBAAoB;IACpB,YAAY;AAChB;;AAEA;IACI,aAAa;AACjB;;AAEA;IACI,qBAAqB;IACrB,sBAAsB;IACtB,eAAe;AACnB;;AAEA;IACI,kBAAkB;IAClB,WAAW;IACX,gBAAgB;IAChB,QAAQ;IACR,OAAO;IACP,aAAa;IACb,cAAc;IACd,mBAAmB;IACnB,oBAAoB;IACpB,kBAAkB;IAClB,oBAAoB;IACpB,kBAAkB;IAClB,gBAAgB;IAChB,eAAe;AACnB;;AAEA;IACI,eAAe;IACf,sBAAsB;IACtB,oBAAoB;IACpB,OAAO;IACP,iBAAiB;AACrB;;AAEA;IACI,gBAAgB;IAChB,SAAS;IACT,UAAU;AACd;;AAEA;IACI,YAAY;IACZ,YAAY;IACZ,kBAAkB;AACtB;;AAEA;IACI,UAAU;AACd;;AAEA;IACI,cAAc;AAClB;;AAEA;IACI,aAAa;IACb,8BAA8B;IAC9B,WAAW;AACf;AACA;IACI,YAAY;IACZ,gBAAgB;IAChB,uBAAuB;IACvB,mBAAmB;AACvB","sourcesContent":["/* ===== SEARCH BOX ===== */\n.search {\n    display: flex;\n    justify-content: end;\n}\n\n.search_content {\n    width: 100%;\n    border-radius: 0.7em;\n}\n\n.search_input_container {\n    justify-content: end;\n    align-content: center;\n    display: flex;\n}\n\n.open_search_input_btn {\n    display: flex;\n    justify-content: end;\n    padding: 12px 26px 12px 26px;\n}\n\n.clear_search_input_btn {\n    cursor: pointer;\n    justify-content: center;\n    align-items: center;\n    position: absolute;\n    margin-top: 1ex; /* equal to search_input top padding+ search top padding */\n}\n\n.search_input {\n    width: 100%;\n    border-radius: 0.7em;\n    padding: 1ex;\n}\n\n.search_input:focus {\n    outline: none;\n}\n\n.search_row {\n    list-style-type: none;\n    font-family: monospace;\n    cursor: pointer;\n}\n\n.search_result_list {\n    position: absolute;\n    z-index: 30;\n    list-style: none;\n    right: 0;\n    left: 0;\n    margin-top: 0;\n    margin-left: 0;\n    padding-left: 1.2em;\n    padding-